@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

:root {
  --color-pass: #4caf50;
  --color-fail: #f44336;
  --color-default: #e0e0e0;
}

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  background: linear-gradient(to bottom, #ffb347, #ffcc33);
  font-family: 'Poppins', sans-serif;
}

.page {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.container {
  background: rgba(255,255,255,0.85);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  padding: 30px 25px;
  border-radius: 20px;
  box-shadow: 0 8px 30px rgba(0,0,0,0.1);
  text-align: center;
  position: relative; /* For streak counter and fireworks */
}

h1 {
  color: #333;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.btn {
  background: linear-gradient(45deg, #ff6ec4, #7873f5);
  border: none;
  border-radius: 25px;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  margin: 0.5rem;
  padding: 12px 28px;
  transition: transform 0.2s ease, opacity 0.2s ease;
}

.btn:hover {
  transform: scale(1.05);
  opacity: 0.85;
}

.input {
  width: 100%;
  padding: 10px 12px;
  font-size: 1rem;
  border: 2px solid #ccc;
  border-radius: 8px;
  margin-bottom: 1rem;
  transition: border 0.3s;
}

.input:focus {
  outline: none;
  border-color: #7873f5;
}

.calendar {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 8px;
  margin: 1rem 0;
}

.day-box {
  background-color: var(--color-default);
  border-radius: 8px;
  color: white;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  cursor: default;
  transition: transform 0.2s, background-color 0.3s;
}

.day-box:hover {
  transform: scale(1.1);
}

.streak-counter {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #ff4081;
  text-shadow: 0 0 5px #ff4081;
  z-index: 10;
}

.btn-ram {
  background: linear-gradient(45deg, #3b82f6, #2563eb);
  border: none;
  border-radius: 25px;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  margin: 0.5rem;
  padding: 12px 28px;
  transition: transform 0.2s ease, opacity 0.2s ease;
}

.btn-ram:hover {
  transform: scale(1.05);
  opacity: 0.85;
}

.congrats-overlay {
  position: fixed;
  inset: 0;
  height: 100vh;
  background-color: rgba(0,0,0,0.85);
  color: white;

  display: flex !important;
  flex-direction: column !important;
  align-items: center !important;
  justify-content: center !important;

  z-index: 9999;
  overflow: visible;
  padding: 2rem;
  text-align: center;
}

.congrats-content-wrapper {
  /* Make sure this wrapper does not stretch vertically beyond needed */
  display: flex !important;
  flex-direction: column !important;
  align-items: center !important;
  justify-content: center !important;

  width: 100%;
  max-width: 400px;
  margin: 0 auto;

  /* Important: remove height to avoid unwanted stretching */
  height: auto !important;
}


/* Congrats gif spinning animation */
.congrats-gif {
  width: 360px; /* bigger */
  max-width: 80%;
  border-radius: 12px;
  margin-bottom: 2rem;
  box-shadow: 0 8px 30px rgba(0,0,0,0.2);
  animation: spin 6s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Fireworks container inside congrats overlay */
.fireworks-container {
  position: absolute;
  inset: 0;
  pointer-events: none;
  overflow: visible;
  z-index: 10000; /* Above overlay content */
  animation: fireworkColorChange 6s infinite alternate;
}

/* Each firework burst wrapper */
.firework {
  position: absolute;
  width: 10px;
  height: 10px;
  pointer-events: none;
  overflow: visible;
  filter: drop-shadow(0 0 5px rgba(255,255,255,0.8));
}

/* Individual firework particle */
.firework-particle {
  position: absolute;
  width: 5px;
  height: 5px;
  background: radial-gradient(circle, var(--firework-color, #ffeb3b) 60%, transparent 80%);
  border-radius: 50%;
  opacity: 1;
  transform: translate(0,0) scale(1);
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  animation-name: fireworkParticleBurst;
  animation-duration: 1.3s;
  animation-iteration-count: 1;
}

/* Particle animation - small burst, fading and falling slightly */
@keyframes fireworkParticleBurst {
  0% {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
  100% {
    opacity: 0;
    transform: translate(var(--x), var(--y)) scale(0.5);
  }
}

/* Flashing congratulations text */
.flash-text {
  font-size: 4.5rem; /* bigger */
  font-weight: 900;
  color: #ffeb3b;
  text-shadow: 0 0 15px #ffeb3b, 0 0 30px #ffe57f;
  animation: flash 6s infinite alternate, flashColorChange 6s infinite alternate;
  margin-bottom: 2rem;
}

@keyframes flash {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
}

/* Animate text color and shadow to cycle through colors */
@keyframes flashColorChange {
  0% {
    color: #ffeb3b;
    text-shadow: 0 0 15px #ffeb3b, 0 0 30px #ffe57f;
  }
  25% {
    color: #ff4081;
    text-shadow: 0 0 15px #ff4081, 0 0 30px #ff80ab;
  }
  50% {
    color: #4caf50;
    text-shadow: 0 0 15px #4caf50, 0 0 30px #80e27e;
  }
  75% {
    color: #2196f3;
    text-shadow: 0 0 15px #2196f3, 0 0 30px #6ec6ff;
  }
  100% {
    color: #ffeb3b;
    text-shadow: 0 0 15px #ffeb3b, 0 0 30px #ffe57f;
  }
}

/* Small navigation buttons (optional) */
.small-btn {
  padding: 8px 16px;
  font-size: 0.85rem;
  margin: 0 5px;
}


